import React ,{ Component } from 'react';
import {
    StyleSheet,
    Text,
    View,
    ScrollView,
    ListView,
    Image,
} from 'react-native';

var Dimensions = require('Dimensions');
var {width} = Dimensions.get('window');

// json
var TopMenu = require('../../LocalData/TopMenu.json');
//
// 
import TopListView from './XMCTopListView'

export default class TopView extends Component {

    state = {
        activePage: 0
    }

    componentDidMount() {
        // alert(TopMenu.data[0].title);
    }

    render() {
        return (
          <View style={styles.container}>
              {/**/}
              <ScrollView
                horizontal={true}
                pagingEnabled={true}
                showsHorizontalScrollIndicator={false}
                onMomentumScrollEnd = {this.onScrollAnimationEnd}
              >
                  {this.renderScrollItem()}
              </ScrollView>
              {/**/}
              <View style={styles.indicatorViewStyle}>
                  {this.renderIndicator()}
              </View>
          </View>
        );
    }

    // 
    onScrollAnimationEnd=(e)=>{
        // 
        var currentPage = Math.floor(e.nativeEvent.contentOffset.x / width);
        this.setState({
            activePage: currentPage,
        })
    }

    // scrollView
    renderScrollItem(){
        // 
        var itemArr = [];
        //  ---> 
        var dataArr = TopMenu.data;
        // 
        for(var i=0; i<dataArr.length; i++){
            itemArr.push(
              <TopListView key={i} dataArr={dataArr[i]}/>
            );
        }
        return itemArr;

        // var colorArr = ['red','green'];
        // // 
        // for(var i=0; i<colorArr.length; i++){
        //     itemArr.push(
        //       <View key={i} style={{backgroundColor:colorArr[i],width:width,height:120}}>
        //           <Text>{i}</Text>
        //       </View>
        //     );
        // }
        //    
        //    return itemArr;
    }

    // ()
    renderIndicator(){
        // 
        var indicatorArr = [], style;
        // 
        for(var i=0; i<2; i++){
            // 
            style = (i === this.state.activePage) ? {color:'orange'} :  {color:'gray'}
            indicatorArr.push(
              <Text key={i} style={[{fontSize:30}, style]}>&bull;</Text>
            );
        }
        // 
        return indicatorArr;
    }
}


const styles = StyleSheet.create({
    container: {
       backgroundColor:'white'
    },

    indicatorViewStyle:{
       // 
        flexDirection:'row',
       // 
        justifyContent:'center'
    }
});


