import React, {Component} from 'react';
import {StyleSheet, Text, View, Image, Platform} from 'react-native';
import {Navigator} from 'react-native-deprecated-custom-components';

/*------------*/
import TabNavigator from 'react-native-tab-navigator';
import Home from './../Home/XMCHome';
import Shop from './../Shop/XMCShop';
import Mine from './../Mine/XMCMine';
import More from '../More/XMCMore';

export default class Main extends Component {

  // (,)
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'home',
    };
  }

  render() {
    return (
      <TabNavigator>
        {/*----*/}
        {this.renderTabBarItem('', require('./../../image/home.png'), require('./../../image/home_selected.png'), 'home', '', Home)}
        {/*----*/}
        {this.renderTabBarItem('', require('./../../image/shop.png'), require('./../../image/shop_selected.png'), 'shop', '', Shop)}
        {/*----*/}
        {this.renderTabBarItem('', require('./../../image/mine.png'), require('./../../image/mine_selected.png'), 'mine', '', Mine)}
        {/*----*/}
        {this.renderTabBarItem('', require('./../../image/more.png'), require('./../../image/more_selected.png'), 'more', '', More)}
      </TabNavigator>
    );
  }

  // TabBarItem
  renderTabBarItem(title, iconName, selectedIconName, selectedTab, componentName, component,badgeText) {
    return <TabNavigator.Item
      title={title} // , {}
      renderIcon={() => <Image source={iconName} style={styles.iconStyle}/>} // 
      renderSelectedIcon={() => <Image source={selectedIconName} style={styles.iconStyle}/>} // 
      selected={this.state.selectedTab === selectedTab}
      onPress={() => this.setState({selectedTab: selectedTab})}
      selectedTitleStyle={styles.selectedTitleStyle}
      badgeText={badgeText}
    >
      <Navigator
        initialRoute={{
          name: componentName,
          component: component
        }}
        configureScene={() => {
          return Navigator.SceneConfigs.PushFromRight;
        }}
        renderScene={(route, navigator) => {  // , 
          let Component = route.component; // 
          return <Component navigator={navigator} {...route.passProps}/>; // 
        }}
      />
    </TabNavigator.Item>;
  }
}

const styles = StyleSheet.create({
  iconStyle: {
    width: Platform.OS === 'ios' ? 30 : 25,
    height: Platform.OS === 'ios' ? 30 : 25
  },
  selectedTitleStyle:{
    color:'#d81e06',
  }
});
