import React ,{ Component } from 'react';
import {
    StyleSheet,
    Text,
    View,
    ScrollView,
    ListView,
    Image,
    TouchableOpacity,
    Platform
} from 'react-native'

var Dimensions = require('Dimensions');
var {width} = Dimensions.get('window');

// 
var cols = 5;
var cellW = Platform.OS == 'ios' ? 70: 60;
var cellH = 70;
var vMargin = (width - cellW * cols) / (cols + 1);

// RN----
console.disableYellowBox = true;
export default class TopListView extends Component {

    static defaultProps() {
        return {
            dataArr: []
        }
    };
    constructor(props) {
        super(props);
        const ds = new ListView.DataSource({rowHasChanged: (r1, r2) => r1 !== r2});
        this.state = {
            dataSource: ds.cloneWithRows(this.props.dataArr)
        };
    }

    render() {
        return (
          <View style={styles.container}>
              <ListView
                        dataSource={this.state.dataSource}
                        renderRow={this.renderRow}
                        contentContainerStyle={styles.contentViewStyle}
                        scrollEnabled={false}
              />
          </View>
        );
    }

    // Item,cell
    //rowdata.image
    // this.renderRow4rowData, sectionID, rowID, highlightRow
    renderRow(rowData){
        return(
          <TouchableOpacity>
              <View style={styles.cellStyle}>
                  <Image source={{uri:rowData.imageUrl}} style={{width:52, height:52}}/>
                  <Text style={styles.titleStyle}>{rowData.title}</Text>
              </View>
          </TouchableOpacity>
        );
    }
}


const styles = StyleSheet.create({
    contentViewStyle:{
        // 
        flexDirection:'row',
        // cell
        flexWrap:'wrap',
        // 
        width:width
    },

    cellStyle:{
        // backgroundColor:'red',
        width:cellW,
        height:cellH,
        // 
        justifyContent:'center',
        alignItems:'center',
        marginTop:10,
        marginLeft:vMargin
    },

    titleStyle:{
        fontSize:Platform.OS == 'ios' ? 14 : 12,
        color:'gray'
    }
});


