import React, {Component} from 'react'
import {StyleSheet, Text, View, Image, TouchableOpacity, ScrollView, Platform} from 'react-native'
import CommonCell from "./XMCCommonCell"

/*----*/

export default class More extends Component {
  render() {
    return (
      <View style={styles.container}>
        {/**/}
        {this.renderNavBar()}
        <ScrollView>
          <View style={{marginTop: 20}}>
            <CommonCell title=""/>
          </View>

          <View style={{marginTop: 20}}>
            <CommonCell title="" isSwitch={true}/>
            <CommonCell title=""/>
            <CommonCell title="" rightTitle='1.99M'/>
            <CommonCell title=""/>
            <CommonCell title=""/>
            <CommonCell title=""/>
          </View>
          <View style={{marginTop: 20}}>
            <CommonCell title=""/>
            <CommonCell title=""/>
            <CommonCell title=""/>
          </View>
        </ScrollView>
      </View>
    )
  }

  // 
  renderNavBar() {
    return <View style={styles.navOutViewStyle}>
      <Text style={{color: 'white', fontSize: 20,  }}></Text>
      <TouchableOpacity
       activeOpacity={0.5}
       style={styles.rightViewStyle}>
        <Image source={require('./../../image/setting.png')} style={styles.navImgStyle}></Image>
      </TouchableOpacity>
    </View>
  }
}

const styles = StyleSheet.create({
  navImgStyle: { // 
    width: Platform.OS === 'ios' ? 30 : 28,
    height: Platform.OS === 'ios' ? 30 : 28,
  },
  rightViewStyle: {
    //
    position: 'absolute',
    right: 10,
    bottom: Platform.OS === 'ios' ? 15 : 5,
  },
  navOutViewStyle: {
    height: Platform.OS === 'ios' ? 64 : 44,
    backgroundColor: '#00A7FF',
    //
    flexDirection: 'row',
    // ---
    alignItems: 'center',
    // 
    justifyContent: 'center'
  },
  container: {
    flex: 1,
    backgroundColor: '#e8e8e8',
  },
  welcome: {
    fontSize: 20,
    textAlign: 'center',
    margin: 10,
  },
})
