import React, {Component} from 'react';
import {StyleSheet, Text, View, TouchableOpacity, TextInput, Image, ScrollView, Platform, ListView} from 'react-native'
/*----*/
import HomeDetail from "./XMCHomeDetail";
import TopView from "./XMCTopView"
import HomeMiddleView from "./XMCHomeMiddleView"
import MiddleBottomView from "./XMCMiddleBottomView"
import ShopCenter from "./XMCShopCenter"
import ShopCenterDetail from "./XMCShopCenterDetail"
import GeustYouLike from "./XMCGeustYouLike"

var Dimensions = require('Dimensions');
var {width, height} = Dimensions.get('window');
export default class Home extends Component {
  render() {
    return (
      <View style={styles.container}>
        {/**/}
        {this.renderNavBar()}
        {/**/}
        <ScrollView>
          {/* View*/}
          {/*<TopView/>*/}
          {/**/}
          <HomeMiddleView/>
          {/**/}
          <MiddleBottomView
            // popTopHome={(data)=>{this.pushToDetail(data)}}
          />
          {/**/}
          <ShopCenter
            popToHomeView={(data)=>{this.pushToShopCenterDetail(data)}}
          />
          {/**/}
          <GeustYouLike />
        </ScrollView>
     {/*   <TouchableOpacity
          onPress={() => {
            this.pushToDetail(); // 
          }}
        >
          <Text style={styles.welcome}></Text>
        </TouchableOpacity>*/}
      </View>
    );
  }

  // // 
  // pushToDetail() {
  //   this.props.navigator.push({
  //     component: HomeDetail, // 
  //     title: ''
  //   });
  // }

  renderNavBar() {
    return (
      <View style={styles.navBarStyle}>
        {/**/}
        <TouchableOpacity
          // onPress={() => this.pushToDetail()}
        >
          <Text style={{color: 'white'}}></Text>
        </TouchableOpacity>

        {/**/}
        <TextInput
          placeholder=",,"
          style={styles.topInputStyle}
        />

        {/**/}
        <View style={styles.rightNavViewStyle}>
          <TouchableOpacity
            // onPress={() => {
            //   alert(''); // 
            // }}
          >
            <Image source={require('./../../image/bell.png')} style={styles.navRightImgStyle}/>
          </TouchableOpacity>

          <TouchableOpacity
            onPress={() => {
              alert(''); // 
            }}
          >
            <Image source={require('./../../image/scan.png')} style={styles.navRightImgStyle}/>
          </TouchableOpacity>

        </View>
      </View>
    );
  }

  // 
  pushToShopCenterDetail(url) {
    this.props.navigator.push(
      {
        component: ShopCenterDetail, // 
        passProps: {'url': this.dealWithUrl(url)}
      }
    );
  }
  // URL
  dealWithUrl(url){
    return url.replace('imeituan://www.meituan.com/web/?url=', '');
  }
}

const styles = StyleSheet.create({
  navBarStyle: {// 
    height: Platform.OS==='ios'?64:44,
    backgroundColor: '#00A7FF',
    //
    flexDirection: 'row',
    // ---
    alignItems: 'center',

    // 
    justifyContent: 'space-around'
  },
  rightNavViewStyle: {
    flexDirection: 'row',
    height: 64,
    // 
    alignItems: 'center'
  },
  topInputStyle: { // 
    width: width * 0.71,
    height: Platform.OS==='ios'?35:35,
    backgroundColor: 'white',
    marginTop:Platform.OS==='ios'?18:0,
    // 
    borderRadius: 18,
    //
    paddingLeft: 10,
    //
    paddingTop: 8
  },
  navRightImgStyle: { // 
    width: Platform.OS==='ios'?30:28,
    height: Platform.OS==='ios'?30:28,
  },

  container: {
    flex: 1,
    backgroundColor: '#e8e8e8',
  },
  welcome: {
    fontSize: 20,
    textAlign: 'center',
    margin: 10,
  },

});
