import React, {Component} from 'react'
import {StyleSheet, Text, View, Image, TouchableOpacity, Switch, Platform} from 'react-native'


export default class MyCell extends Component {

  static defaultProps() {
    return {
      leftIcon:'',
      leftTitle:'',
      rightIcon:'',
      rightTitle:'',
    }
  };

  state = {
    isOn: false
  }

  render() {
    return (
      <TouchableOpacity
        activeOpacity={0.5}
      >
        <View style={styles.container}>
          {/**/}
          <View style={styles.leftViewStyle}>
            <Image source={this.props.leftIcon} style={styles.leftImgStyle}></Image>
            <Text style={styles.leftTitleStyle}>{this.props.leftTitle}</Text>
          </View>
          {/**/}
          <View style={styles.rightViewStyle}>
            {this.rightSubView()}
          </View>
        </View>
      </TouchableOpacity>
    )
  }

  // 
  rightSubView() {
    return(
      <View style={{flexDirection:'row',alignItems:'center'}}>
        {this.rightRightContent()}
        {/**/}
        <Image source={require('./../../image/arrow.png')} style={{width: 22, height: 22, marginRight: 8}}/>
      </View>
    )
  }

  // 
  rightRightContent() {
    if(!this.props.rightIcon){// 
      return (
        <Text style={{color:'gray', }}>{this.props.rightTitle}</Text>
      )
    }else {
      return (
        <Image source={this.props.rightIcon} style={{width: 40, height: 40, marginLeft:5,marginRight: 8}}/>
      )
    }
  }
}

const styles = StyleSheet.create({
  container: {
    // 
    flexDirection:'row',
    // 
    alignItems:'center',
    // 
    justifyContent:'space-between',

    //
    backgroundColor: 'white',
    //
    height: Platform.OS === 'ios' ? 40 : 45,

    // 
    borderBottomColor:'#e8e8e8',
    borderBottomWidth:0.5
  },
  leftViewStyle:{
    // 
    flexDirection:'row',
    // 
    alignItems:'center',
    // 
    marginLeft: 8
  },
  rightViewStyle:{
  },
  leftImgStyle:{// 
    width: 24,
    height: 24,
    marginRight: 6,
    // 
    borderRadius:12
  },
  leftTitleStyle:{
    fontSize: 16,
    fontWeight: 'bold',
    marginLeft: 8,
  }
})
